html, body, ul, ol, li, form, fieldset, legend, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}
fieldset, img {
  border: 0;
}
li {
  list-style: none;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-size: 0.6666666px;
}
body {
  font-family: Roboto;
}
.show-enter, .show-leave-to {
  opacity: 0;
}
.show-enter-to, .show-leave {
  opacity: 1;
}
.drive-up-enter, .drive-up-leave-to {
  -webkit-transform: translate3d(0, 468rem, 0);
  transform: translate3d(0, 468rem, 0);
}
.drive-up-enter.translate, .drive-up-leave-to.translate {
    -webkit-transform: translate(0, 468rem, 0);
    transform: translate(0, 468rem, 0);
}
.drive-up-enter-to, .drive-up-leave {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.drive-up-enter-to.translate, .drive-up-leave.translate {
    -webkit-transform: translate(0, 0, 0);
    transform: translate(0, 0, 0);
}
.drive-up-enter-active {
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}
.drive-up-leave-active {
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}
.title-transition-enter, .title-transition-leave-to {
  opacity: 0;
}
.title-transition-enter-to, .title-transition-leave {
  opacity: 1;
}
.title-transition-enter-active {
  -webkit-transition: opacity .4s ease-in;
  transition: opacity .4s ease-in;
}
.title-transition-leave-active {
  -webkit-transition: opacity 0.4s cubic-bezier(0, 1, 0, 1);
  transition: opacity 0.4s cubic-bezier(0, 1, 0, 1);
}
.no-transition-animation {
  -webkit-transition-duration: 0s !important;
          transition-duration: 0s !important;
  -webkit-transition: -webkit-transform 0s ease !important;
  transition: -webkit-transform 0s ease !important;
  transition: transform 0s ease !important;
  transition: transform 0s ease, -webkit-transform 0s ease !important;
}
.no-shadow {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}
.no-scale {
  -webkit-transform: scale3d(1, 1, 1) !important;
          transform: scale3d(1, 1, 1) !important;
}
.gui-wrapper {
  height: 1080rem;
  overflow: hidden;
  position: relative;
  width: 1920rem;
}
.fake-video {
  position: absolute;
  width: 100%;
  z-index: -1;
}
.live-flag {
  font-size: 20rem;
  background: #c02114;
  color: white;
  font-weight: 400;
  border-radius: 4px;
  padding: 2px 10px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}



